<template>
  <div class="bgc-white">
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(item, index) in imgBox" :key="index">
        <a :href="'#/activity?id=' + item.id">
          <img class="slide_img" v-lazy="item.img.op_spic"/>
          <div class="p10">
            <div class="text-overflow">{{ item.name }}</div>
            <div class="f14 gray text-overflow">{{ item.introduce }}</div>
          </div>
        </a>
      </van-swipe-item>
    </van-swipe>
    <div class="box px-tb pl10 p5 mt5">
      <div class="union-flash"></div>
      <van-swipe :autoplay="3000" vertical :show-indicators="showIndicators" class="rollFlash bf1">
        <van-swipe-item v-for="(item, index) in rollFlash" :key="index">
          <div class="ml25 text-overflow" @click="toNewsList()">{{ item.title }}</div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="p15 pt10">
      <div class="box">
        <router-link class="bf1 member-card" :to="{name: 'member'}"></router-link>
        <router-link class="bf1 member-card" :to="{name: 'about'}"></router-link>
      </div>
      <div class="scrollWrap px-b">
        <div class="scrollXbox box pb15">
          <a href="#/service-item?serviceName=全球产业视野" class="tc mr10 pt5">
            <img src="@/assets/images/industryUnion/views.png" width="50" alt="">
            <div class="f12 gray mt5">全球产业视野</div>
          </a>
          <a href="#/service-item?serviceName=政企对接平台" class="tc mr15 pt5">
            <img src="@/assets/images/industryUnion/gov.png" width="50" alt="">
            <div class="f12 gray mt5">政企对接平台</div>
          </a>
          <a href="#/service-item?serviceName=产融投资" class="tc mr15 pt5">
            <img src="@/assets/images/industryUnion/invest.png" width="50" alt="">
            <div class="f12 gray mt5">产融投资</div>
          </a>
          <a href="#/service-item?serviceName=产业服务输出" class="tc mr15 pt5">
            <img src="@/assets/images/industryUnion/export.png" width="50" alt="">
            <div class="f12 gray mt5">产业服务输出</div>
          </a>
          <a href="#/service-item?serviceName=全球资源导入" class="tc mr15 pt5">
            <img src="@/assets/images/industryUnion/import.png" width="50" alt="">
            <div class="f12 gray mt5">全球资源导入</div>
          </a>
          <a href="#/service-grid" class="tc mr15 pt5">
            <img src="@/assets/images/industryUnion/allservice.png" width="50" alt="">
            <div class="f12 gray mt5">全部服务</div>
          </a>
        </div>
      </div>
      <div class="head_title mb10">
        <div class="box-justify">
          <div class="f18">企业速递</div>
          <a class="red f14 ib" href="#/firmtop">查看全部</a>
        </div>
        <div class="gray f14">联盟新伙伴，打造共赢合作新方式</div>
      </div>
      <router-link :to="{path: '/firmtop-detail', query: {id: item.firm_id}}" tag="div"
                   class="delivery box pt10 pb10 px-b" v-for="item in delivery"
                   :key="item.firm_id">
        <img v-lazy="item.imgUrl.op_spic" width="76" height="76" alt="">
        <div class="pl10 pr10 bf1">
          <div>{{ item.firm_name }}</div>
          <p class="f14 gray mt5 text-overflow-line2">{{ item.firm_lead }}</p>
        </div>
      </router-link>
      <div class="head_title mt20">
        <div class="box-justify">
          <div class="f18">联盟专访</div>
          <a class="red f14 ib" href="#/interview-list">查看全部</a>
        </div>
        <div class="gray f14">行业交流，产业未来，观点碰撞</div>
      </div>
      <div class="box mt15">
        <a :href="'#/interview-detail?id=' + zf.id" v-for="zf in twoZF" class="news-box bf1" :key="zf.id">
          <img v-lazy="zf.pic.op_spic" height="100" alt="">
          <div class="mt10 f14 text-overflow-line2">{{ zf.title }}</div>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'Index',
    data() {
      return {
        userInfo: {},
        showIndicators: false,
        imgBox: [],
        rollFlash: [],
        delivery: [],
        twoZF: [],
        allService: []
      }
    },
    created() {
      this.init()
      setTimeout(() => {
        this.$store.dispatch('UPDATE_LOADINGSTATE', false)
      }, 1500)
    },
    activated() {
      this.$store.dispatch('UPDATE_LOADINGSTATE', false)
      // this.getBanner()
    },
    methods: {
      async init() {
        let res = await this.axios.post(this.apiConfig.getUserInfo)
        this.userInfo = res.d
        this.$store.dispatch('UPDATE_USERINFO', res.d)
        this.getBanner()
        this.getRollFlash()
        this.get2Delivery()
        this.getTwoZF()
      },
      // 联盟活动banner
      getBanner() {
        this.axios.get(this.apiConfig.unionBanner, {
          params: {zone_id: this.userInfo.zid}
        }).then(res => {
          if (res.s === 1) {
            this.imgBox = res.d
          }
        })
      },
      getRollFlash() {
        this.axios.get(this.apiConfig.rollFlash, {
          params: {zone_id: this.userInfo.zid}
        }).then(res => {
          if (res.s === 1) {
            this.rollFlash = res.d
          }
        })
      },
      get2Delivery() {
        this.axios.get(this.apiConfig.get2Delivery).then(res => {
          if (res.s === 1) {
            this.delivery = res.d
          }
        })
      },
      getTwoZF() {
        let zone_id = this.userInfo.zid
        this.axios.get(this.apiConfig.getTwoZF + '?zone_id=' + zone_id).then(res => {
          if (res.s === 1) {
            this.twoZF = res.d
          }
        })
      },
      toNewsList() {
        this.$router.push({
          name: 'newsList'
        })
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .van-cell {
      &:not(:last-child) {
        &::after {
          display: none;
        }
        .van-cell__title {
          margin-left: rem(-15px);
        }
        .van-cell__value {
          padding-right: rem(15px);
          margin-left: rem(-15px);
        }
        .van-cell__right-icon {
          margin-right: rem(15px);
        }
        margin-left: rem(15px);
        border-bottom: 0;
        background-repeat: repeat-x;
        background-size: 100% 1px;
        background-position: 0 100%;
        background-image: -webkit-gradient(linear, left top, left bottom, color-stop(.5, transparent), color-stop(.5, #f6f6f6), to(#f6f6f6));
      }
    }
  }

  #app {
    .van-swipe__indicators {
      right: rem(15px);
      transform: none;
      bottom: rem(74px);
      left: auto;
    }
    .van-cell {
      font-size: rem(16px);
      padding: rem(15px);
      textarea {
        min-height: rem(50px);
        line-height: 1.6;
      }
    }
    .van-field .van-cell__title {
      max-width: rem(90px);
    }

    .van-cell img {
      margin-left: rem(6px);
    }
  }

  .rollFlash {
    height: rem(46px);
    .van-swipe-item {
      height: rem(46px);
      line-height: rem(46px);
    }
  }

  .scrollWrap {
    overflow: auto;
    margin-bottom: rem(20px);
    -webkit-overflow-scrolling: touch;
  }

  .scrollXbox {
    width: rem(502px);
  }

  .union-flash {
    width: rem(76px);
    height: rem(46px);
    background: url(../../../assets/images/industryUnion/union_flash.png) no-repeat center;
    background-size: 100%;
  }

  .head_title {
    padding: 0 0 rem(13px) rem(18px);
    background: url(../../../assets/images/industryUnion/title_left.png) left top / 0.08rem 76% no-repeat, url(../../../assets/images/industryUnion/title_down.png) bottom right / 97% 0.16rem no-repeat;
  }

  .member-card {
    margin-left: rem(-10px);
    margin-right: rem(10px);
    background: url('../../../assets/images/industryUnion/left.png') no-repeat center;
    background-size: 100%;
    height: rem(120px);
    &:nth-child(2) {
      margin-right: rem(-10px);
      background-image: url(../../../assets/images/industryUnion/right.png);
    }
  }

  .slide_img {
    width: 100%;
    height: rem(170px);
  }

  .news-box {
    &:last-child {
      margin-left: rem(20px);
    }
    img {
      width: 100%;
      height: rem(100px);
    }
  }

  .delivery img {
    display: block;
    width: rem(76px);
    height: rem(76px);
  }
</style>
