import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: '',
    loadingState: true
  },
  mutations: {
    UPDATE_USERINFO(state, data) {
      state.userInfo = data
    },
    UPDATE_LOADINGSTATE(state, data) {
      state.loadingState = data
    }
  },
  actions: {
    UPDATE_USERINFO({commit}, data) {
      commit('UPDATE_USERINFO', data)
    },
    UPDATE_LOADINGSTATE({commit}, data) {
      commit('UPDATE_LOADINGSTATE', data)
    }
  }
})
