import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'
import NotFound from '@/views/notfound.vue'

const NewsList = () => import('./views/news-list.vue') // 新闻列表
const NewsDetail = () => import('./views/news-detail.vue') // 新闻详情
const InterviewList = () => import('./views/interview-list.vue') // 联盟专访列表
const Activity = () => import('@/views/activity.vue') // 联盟活动
const Member = () => import('./views/member.vue') // 成为会员
const About = () => import('./views/about.vue') // 关于联盟
const ServiceGrid = () => import('./views/service-grid.vue') // 全部服务
const ServiceItem = () => import('./views/service-item.vue') // 服务详情
const ServiceApply = () => import('./views/service-apply.vue') // 申请服务
const ServiceSubmit = () => import('./views/service-submit.vue') // 提交服务
const Firmtop = () => import('./views/firmtop.vue') // 企业速递详情
const FirmtopDetail = () => import('./views/firmtop-detail.vue') // 企业速递详情
Vue.use(Router)

export default new Router({
  routes: [{
    path: '',
    name: 'Index',
    component: Index,
    meta: {
      pageTitle: '产业联盟',
      keepAlive: true
    }
  }, {
    path: '/news-list',
    name: 'newsList',
    component: NewsList,
    meta: {
      pageTitle: '新闻列表'
    }
  }, {
    path: '/news-detail',
    name: 'newsDetail',
    component: NewsDetail,
    meta: {
      pageTitle: '新闻详情'
    }
  }, {
    path: '/interview-list',
    name: 'interviewList',
    component: InterviewList,
    meta: {
      pageTitle: '联盟专访列表'
    }
  }, {
    path: '/interview-detail',
    name: 'interviewDetail',
    component: NewsDetail,
    meta: {
      pageTitle: '联盟专访详情'
    }
  }, {
    path: '/activity',
    name: 'activity',
    component: Activity,
    meta: {
      pageTitle: '活动详情'
    }
  }, {
    path: '/member',
    name: 'member',
    component: Member,
    meta: {
      pageTitle: '成为会员'
    }
  }, {
    path: '/about',
    name: 'about',
    component: About,
    meta: {
      pageTitle: '关于联盟'
    }
  }, {
    path: '/service-grid',
    name: 'serviceGrid',
    component: ServiceGrid,
    meta: {
      pageTitle: '全部服务'
    }
  }, {
    path: '/service-item',
    name: 'serviceItem',
    component: ServiceItem
  }, {
    path: '/service-apply',
    name: 'serviceApply',
    component: ServiceApply
  }, {
    path: '/service-submit',
    name: 'serviceSubmit',
    component: ServiceSubmit
  }, {
    path: '/firmtop',
    name: 'firmtop',
    component: Firmtop,
    meta: {
      pageTitle: '企业速递'
    }
  }, {
    path: '/firmtop-detail',
    name: 'firmtopDetail',
    component: FirmtopDetail,
    meta: {
      pageTitle: '企业详情'
    }
  }, {
    path: '*',
    component: NotFound,
    name: 'notfound'
  }]
})
