import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Axios from '@/common/request.js'
import ApiConfig from '@/apiConfig.js'
import store from './store.js'
import {
  Cell,
  CellGroup,
  Toast,
  Popup,
  List,
  Field,
  Picker,
  Swipe,
  SwipeItem,
  Search,
  Tag,
  PullRefresh,
  Lazyload,
  Dialog,
  Loading
} from 'vant'
import * as utils from '@/common/utils.js'
import errorImg from '@/assets/images/500.png'
Vue.use(Cell).use(CellGroup).use(Toast).use(Popup).use(List)
    .use(Field).use(Picker).use(Swipe).use(SwipeItem).use(Search)
    .use(Tag).use(PullRefresh).use(Dialog).use(Loading).use(Lazyload, {
  preLoad: 1.3,
  error: errorImg,
  // loading: 'dist/loading.gif',
  attempt: 1
})
Vue.use(ApiConfig)

router.beforeEach((to, from, next) => {
  if (!!to.meta.pageTitle && typeof to.meta.pageTitle !== undefined) {
    utils.setPageTitle(to.meta.pageTitle)
  }
  store.state.loadingState = true
  next()
})
Vue.prototype.axios = Axios
Vue.prototype.ApiConfig = ApiConfig
Vue.config.productionTip = false
document.addEventListener('touchstart', () => {
}, false)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
